import React from "react";
import { withRouter, matchPath } from "react-router";
import { Link } from "react-router-dom";
import FontFaceObserver from "fontfaceobserver";

import {
  withStyles,
  AppBar,
  Tabs,
  Tab,
  Popper,
  ButtonBase,
  Paper,
  Fade,
} from "@material-ui/core";

import { compose } from "mtc/utils";
import ContentContainer from "mtc/components/ContentContainer";

const NAV_ITEMS = [
  { label: "Accueil", path: "/", exact: true },
  {
    label: "Soins",
    path: "/soins",
    items: [
      { label: "Acupuncture", path: "/soins/acupuncture" },
      {
        label: "Tuina",
        path: "/soins/tuina",
        items: [
          { label: "Foo", path: "/soins/tuina/foo" },
          { label: "Bar", path: "/soins/tuina/bar" },
          { label: "Baz", path: "/soins/tuina/baz" },
        ],
      },
      { label: "Ventouses", path: "/soins/ventouses" },
      { label: "Qi-Gong", path: "/soins/qi-gong" },
    ],
  },
  { label: "Tarifs", path: "/tarifs" },
  { label: "Contact", path: "/contact" },
];

const navItemStyles = theme => ({
  root: {
    position: "relative",
    display: "flex",
    flexDirection: "column",
    minHeight: 48,
    zIndex: 100,

    "$popper & ~ &": {
      borderTop: `1px solid rgba(0, 0, 0, 0.3)`,
    },
  },
  popper: {
    display: "flex",
    flexDirection: "column",
    zIndex: 200,

    "& &": {
      marginLeft: -1,
      paddingTop: 1,
    },
  },
  paper: {
    backgroundColor: theme.palette.secondary.main,
  },
});

function _NavItem({ classes, navItem, isNested, className, children }) {
  const rootRef = React.useRef();
  const [isOver, setIsOver] = React.useState(false);

  return (
    <div
      ref={rootRef}
      onMouseEnter={() => setIsOver(true)}
      onMouseLeave={() => setIsOver(false)}
      onClick={() => setIsOver(false)}
      className={classes.root}
    >
      <ButtonBase
        variant="contained"
        color="secondary"
        component={Link}
        to={navItem.path}
        className={className}
      >
        {children}
      </ButtonBase>

      {navItem.items && (
        <Popper
          open={isOver}
          disablePortal
          placement={isNested ? "right-start" : "bottom-start"}
          className={classes.popper}
          anchorEl={rootRef.current}
          transition
        >
          {({ TransitionProps }) => (
            <Fade {...TransitionProps} timeout={350}>
              <Paper square className={classes.paper}>
                {navItem.items.map(navItem => (
                  <Tab
                    key={navItem.path}
                    value={navItem.path}
                    label={navItem.label}
                    component={NavItem}
                    navItem={navItem}
                    isNested
                  />
                ))}
              </Paper>
            </Fade>
          )}
        </Popper>
      )}
    </div>
  );
}

const NavItem = withStyles(navItemStyles)(_NavItem);

const navBarStyles = theme => ({
  root: {
    display: "flex",
    flexDirection: "row",
    padding: `0 ${theme.spacing.unit * 2}px`,
  },
  main: {
    display: "flex",

    [theme.breakpoints.down("xs")]: {
      maxWidth: "100%",
    },
  },
  tabs: {
    flexGrow: 1,
    overflow: "visible",
  },
  tabsFlexContainer: {
    minHeight: 48,
  },
  tabsScroller: {
    overflow: "visible",
  },
  hiddenTab: {
    display: "none",
  },
});

function NavBar({ location, history, classes }) {
  const tabValue = (
    NAV_ITEMS.find(({ path, exact }) =>
      matchPath(location.pathname, { path, exact })
    ) || { path: 0 }
  ).path;

  return (
    <AppBar position="sticky" color="secondary" className={classes.root}>
      <ContentContainer className={classes.main}>
        <Tabs
          value={tabValue}
          indicatorColor="primary"
          textColor="inherit"
          classes={{
            root: classes.tabs,
            flexContainer: classes.tabsFlexContainer,
            scroller: classes.tabsScroller,
          }}
          action={actions => {
            const font = new FontFaceObserver("Fira Sans");
            font.load().then(actions.updateIndicator);
          }}
        >
          <Tab value={0} className={classes.hiddenTab} />

          {NAV_ITEMS.map(navItem => (
            <Tab
              key={navItem.path}
              value={navItem.path}
              label={navItem.label}
              component={NavItem}
              navItem={navItem}
            />
          ))}
        </Tabs>
      </ContentContainer>
    </AppBar>
  );
}

export default compose(
  withRouter,
  withStyles(navBarStyles)
)(NavBar);
